@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #484944;
  
}


.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 1);
}
.expenses-filter{
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}
.date-style{
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #fff;
  background-color:#ba5d6d;
  color:#F3EFCC;
  border-radius: 12px;
  align-items: center;
  justify-content: center; 
}

.expense-date__month{
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year{
  font-size: 0.75rem;
}
.expense-date__day{
  font-size: 1.5rem;
  font-weight: bold;
}
.expense-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin: 1rem 0;
  background-color: #2b2c29;
}

.expense-item__description {
  display: flex;
  justify-content:space-between;
  align-items: center;
  flex: 1 1;
  margin-left: 1rem ;
}

.expense-item h2 {
  font-size: 1rem;
  color: #fff;
}

.expense-item__price{
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color:#9c182f;
  border: 1px solid #fff;
  padding: 0.5rem;
  border-radius: 12px;
}


.expense-list {
 list-style: none;
 padding: 0;
}

.expense-list-empty{
  color: #fff;
  text-align: center;
  
}
.chart-bar{
height: 100%;
display: flex;
flex-direction: column;
align-items: center;
}

.chart-bar__inner{
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill{
  background-color: #7d1326;
  transition: all 0.3s ease-out;
}

.chart-bar__label{
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
.chart {
 padding: 1rem;
 border-radius: 12px;
 background-color: #ba5d6d;
 display: flex;
 justify-content: space-around;
 text-align: center;
 height: 10rem;
}
.expenses {
  padding: 1rem;
  /* background-color: #2b2c29; */
  margin: 2rem auto;
  width: 50rem;

}


  
  
  
  
  

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}


.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
  color: #fff;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #fff;
  width: 20rem;
}

.new-expense__actions {
  text-align: right;
}
.new-expense{
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  text-align: center;
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #9c182f;
  background-color: #9c182f;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: #a62f44;
  border-color: #a62f44;
  transition: 0.3s;
}




