.date-style{
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #fff;
  background-color:#ba5d6d;
  color:#F3EFCC;
  border-radius: 12px;
  align-items: center;
  justify-content: center; 
}

.expense-date__month{
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year{
  font-size: 0.75rem;
}
.expense-date__day{
  font-size: 1.5rem;
  font-weight: bold;
}